html, body {
  margin: 0;
  padding: 0;
  background-color: #3D4B60;
}

#colorContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 400px;
}

#colorContainer > div {
  width: 100%;
  display:flex;
  flex-direction: column;
  position: relative;
}

.colorTitle {
  background-color: #1F2937;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items:center;
  position: absolute;
  bottom: 0px;
  margin: 0px;
  width: 100%;
  height: 46px;
  color: #D5D4D8;
  font-size: 12px;
}

@media (max-width: 500px) {
  #colorContainer {
    height: 754px;
    flex-direction: column;
  }

  #colorContainer > div {
    width: 100%;
    height: 150px;
    flex-direction: row;
  }
  .colorTitle {
    height: 30px;
  }
}

.colorForm {
  height: 90px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: relative;
  box-shadow: 0px 1px 2px 0px #0000000D;
  background-color: #1F2937;
}

.colorPicker {
  height: 42px;
  max-width: 70px;
  border: none;
  background-color: lightgray;
}

.dropDownMenu {
  height: 42px;
  max-width: 312px;
  border-radius: 6px;
  border: 1px solid #D1D5DB;
  color: #D5D4D8;
  background-color: #1F2937;
  font-size: 16px;
  padding: 7px;
  
}

.buttonClick {
  height: 42px;
  max-width: 123px;
  border-radius: 4px;
  border: none;
  background-color: #3D4B60;
  font-size: 12px;
  color: #D5D4D8;
  cursor: pointer;

}

.buttonClick:hover {
  background-color: #14172F;
}
.buttonClick:focus {
  outline: none;
}